input[type=text] {
  padding: 1rem;
}

.navbar-nav {
  --bs-nav-link-padding-y: 0px;
}

.navbar-dark {
  --bs-navbar-active-color: #ffc0cb;
  --bs-navbar-hover-color: #ffc0cb;
  --bs-navbar-color: white;
}

.dropdown-menu {
  --bs-dropdown-bg: black !important;
  --bs-dropdown-link-color: white;
  --bs-dropdown-link-hover-bg: #ffc0cb;
  border-bottom-width: 0.3rem;
  border-bottom-style: solid;
  border-bottom-color: #ffc0cb;
  padding-left: 1rem;
  padding-right: 1rem;
}

.dropdown-menu li a {
  background-position: right bottom;
}

.dropdown-menu li a:hover {
  background: linear-gradient(to left, black 50%, #ffc0cb 50%);
  background-size: 200% 100%;
  background-position: left bottom;
  transition: all .3s ease-out;
  color: black;
}

a.dropdown-item {
  font-size: 1.3rem;
}

active.dropdown-item, .dropdown-item:active {
  background-color: #ffc0cb;
  color: black;
}
body {
  font-family: $typefacePrimary;
  background: $bodyBackgroundColor;
  color: $bodyFontColor;
  font-weight: 400;
  font-style: normal;
  list-style: none;
  padding-left: 0px;
  font-size: 1.3rem;
  line-height: 2rem;
  -webkit-font-smoothing: antialiased;
  -webkit-overflow-scrolling: touch;
}

div.highlight p, p.introduction, p {
  font-family: $typefaceThird;
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 2.1rem;
}

p.introduction {
  margin-bottom: 2rem;
}


p.alt, li.alt {
  font-family: $typefacePrimary;
  font-weight: 400;
  font-style: normal;
  font-size: 1.3rem;
  line-height: 2rem;
}

nav {
  background: $navColor;
  font-family: $typefacePrimary;
  font-size: 1.2rem;
}

body a {
  text-decoration: none;
}

li {
  list-style: none;
}

h2 {
  letter-spacing: 0.1rem;
}

h4 {
  font-size: 1.3rem;
}

h4 span.category {
  border-bottom: thick #ffc0cb;
  border-width: 0.2rem;
  border-bottom-style: solid;
}

.border-coin {
  border-color: #b8b844 !important;
}

.text-theme-blue {
  color:#16384e;
}

.bg-coin {
  background-color: #ffffff !important;
}

.bg-coin:hover {
  color:#16384e;
  background-color:#ffffff !important;
}

.masthead h5 {
  font-size: 1.1rem;
  font-weight: 800;
}

.masthead h1.name {
  margin-top: 8rem;
  font-family: $typefaceName;
  font-weight: 600;
  letter-spacing: 0.3rem;
  font-size: 5rem;
}

h1 {
  font-family: $typefaceSecondary;
  font-weight: 600;
  letter-spacing: 0.1rem;
  font-size: 3rem;
}

h1 span {
  display: inline-block;
  font-weight: 400;
  font-style: italic;
  padding-bottom: 1rem;
  font-size: 2.5rem;
  letter-spacing: 0rem;
}

h2, h3, h4 {
  font-family: $typefaceSecondary;
  font-weight: 600;
}

.cursor-zoom {
  cursor: -moz-zoom-in; 
  cursor: -webkit-zoom-in; 
  cursor: zoom-in;
}

.website-design h1 {
  font-family: "quatro-slab", serif;
  font-weight: 600;
  font-style: normal;
  font-size: 3rem;
}

.website-design h1 span {
  font-family: "quatro-slab", serif;
  font-weight: 400;
  font-style: italic;
}

.btn-hireme i {
  color: pink;
}

.btn-hireme  {
  color: white !important;
  border-color: white;
}

.btn-hireme:hover {
  border-color: white !important;
  color:#ffc0cb !important;
}

