div.statement {
  background-color: $mastBackground;
  color: white;
}

div.statement a {
  color: #ffc0cb;
  text-decoration: underline dotted;
  text-underline-offset: 0.4rem;
}

div.image-caption {
  position: absolute;
  bottom: 0;
  right: 0;
  background-color:rgba(0, 0, 0, 0.7);
  @media (min-width: 700px) {
  width: 25%;
  }
  color: rgba(255, 255, 255, 0.8);
  font-style: italic;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 1.2rem;
}

.btn-primary {
  background-color: $statementColorlanding;
}

.card-body a {
  color:#EC5656 !important;
  border-bottom: 3px;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  border-style: solid;
}

.card-body a:hover {
  color:#16384e !important;
}

//logo
.blink {
  animation: blinker 2.5s linear infinite;
  fill: pink;
}
@keyframes blinker {
  50% {
      opacity: 0;
  }
}

g.circle {
  fill: pink;
  filter: drop-shadow(1px 1px 5px pink);
}

svg#site-logo {
  display: block;
}

nav.navbar g#text{
  transition: all 0.5s ease-out;
  fill: white;
}

// nav.navbar:hover g#text{
//  fill: white;
//  animation: fadeIn;
// transition: all 0.5s ease-in-out;
//}
@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

svg.raygun-logo {
  min-height:80px;
  padding-top: 5px;
  padding-bottom: 5px;
}
g#main_logo {
  fill: white;
}

form {
  transition: all 0.5s ease-out;
}

.send-button {
  $btn-color: #ffc0cb;
  background-color: $btn-color;
  border-color: $btn-color;
  color: black;
}

.send-button:hover {
  background-color: white;
  color: white;
}

.send-button:hover i {
  transform: translateX(10px);
  transition: all 0.5s ease-out;
}